<template>
  <div class="game">
     <list :model="gamename"></list>
  </div>
</template>

<script>
import list from '../list'

export default {
  name: 'game',
  components:{
  	'list':list
  },
  data (){
  	return {
  		gamename:['wuziqi','dezhoupuke']
  	};
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



